@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
    
    overflow-x: hidden;
}

.wish{
    text-align: center;
    font-size: 5em;
    color: #323232;
}
.Avatar{
    background: url(/port/static/media/avatar.11a1de5d.png);
    background-size: 100% 100%;
    height: 42vh;
    width: 20%;
    margin: -4% 40% 4% 40%;
}
.subtext{
    text-align: center;
    font-size: 1.5em;
    color: #323232;
}
.social-icon{
    border-radius: 50%;
    width: 2.5em;
    font-size: 1.8em;
    color: #4d4c7d;
    background-color: transparent;
    /* border: 3px solid #ffac41; */
    border-color: transparent;
    height: 2.5em;
   
    margin: .1em 0.4em;
}
.social-icon:hover{
    background-image: linear-gradient(-90deg, #827397, #d8b9c3);
    color: white;
    /* border-color: #ff1e56; */
    border: 0;
    outline: 0;
}
.social{
    text-align: center;
}
.Stext{
    color: #4d4c7d;
}
.bolding{
    color:#a6b1e1;
}
@media(max-width:800px){

    .wish{
        font-size: 4em;
        
    }
    .Avatar{
        width: 50%;
        height: 32vh;
        margin: -8% 25% 4% 25%;
    }
    .social-icon{
        font-size: 1.2em;
    }
    .social-icon:hover{
        color:#ff1e56;
        background-image: none;
    }
    .particles{
        height: 100%;
    }
}
@media(width:1024px){

    .wish{
        font-size: 4em;
        margin-bottom: 4em;
        
    }
    .Avatar{
        width: 50%;
        height: 32vh;
        margin: -8% 25% 4% 25%;
    }
    .social-icon{
        font-size: 1.2em;
    }
    .social-icon:hover{
        color:#ff1e56;
        background-image: none;
    }
    .particles{
        height: 100%;
    }
}

h1{
    margin-top: 1.5em;
    text-align: center;
    font-family: 'Varela Round', sans-serif;
    font-size: 5em;
    color: #323232ec;
}
p{
    text-align: center;
    font-size: 1.8em;
    font-family: 'Comic Neue', cursive;
    color: #323232ec;
}

@media(max-width:760px){
    h1{
        font-size: 3em;
    }
    p{
        font-size: 1.3em;
    }
}
.skills{
    margin-top: 5em;
}
.progress{
    background-color: #a6b1e1;
    border-top-right-radius: .3em;
    border-bottom-right-radius: .3em;
   
 
 }

 .holder{
    margin: 2.2em 0;
 }
 
 .text{
    font-size: 1.4em;   
 }
.card{
    height: 55vh;
   
