body{
    
    overflow-x: hidden;
}

.wish{
    text-align: center;
    font-size: 5em;
    color: #323232;
}
.Avatar{
    background: url('./avatar.png');
    background-size: 100% 100%;
    height: 42vh;
    width: 20%;
    margin: -4% 40% 4% 40%;
}
.subtext{
    text-align: center;
    font-size: 1.5em;
    color: #323232;
}
.social-icon{
    border-radius: 50%;
    width: 2.5em;
    font-size: 1.8em;
    color: #4d4c7d;
    background-color: transparent;
    /* border: 3px solid #ffac41; */
    border-color: transparent;
    height: 2.5em;
   
    margin: .1em 0.4em;
}
.social-icon:hover{
    background-image: linear-gradient(-90deg, #827397, #d8b9c3);
    color: white;
    /* border-color: #ff1e56; */
    border: 0;
    outline: 0;
}
.social{
    text-align: center;
}
.Stext{
    color: #4d4c7d;
}
.bolding{
    color:#a6b1e1;
}
@media(max-width:800px){

    .wish{
        font-size: 4em;
        
    }
    .Avatar{
        width: 50%;
        height: 32vh;
        margin: -8% 25% 4% 25%;
    }
    .social-icon{
        font-size: 1.2em;
    }
    .social-icon:hover{
        color:#ff1e56;
        background-image: none;
    }
    .particles{
        height: 100%;
    }
}
@media(width:1024px){

    .wish{
        font-size: 4em;
        margin-bottom: 4em;
        
    }
    .Avatar{
        width: 50%;
        height: 32vh;
        margin: -8% 25% 4% 25%;
    }
    .social-icon{
        font-size: 1.2em;
    }
    .social-icon:hover{
        color:#ff1e56;
        background-image: none;
    }
    .particles{
        height: 100%;
    }
}
