@import url('https://fonts.googleapis.com/css2?family=Comic+Neue&family=Varela+Round&display=swap');
h1{
    margin-top: 1.5em;
    text-align: center;
    font-family: 'Varela Round', sans-serif;
    font-size: 5em;
    color: #323232ec;
}
p{
    text-align: center;
    font-size: 1.8em;
    font-family: 'Comic Neue', cursive;
    color: #323232ec;
}

@media(max-width:760px){
    h1{
        font-size: 3em;
    }
    p{
        font-size: 1.3em;
    }
}